@tailwind base;
@tailwind components;
@tailwind utilities;

@keyframes scaling{ from{ scale: 1; } to{ scale:1.2 }}


@keyframes placehold{
    0%{
        background-position: -468px 0
    }
    100%{
        background-position: 468px 0
    }
}

@font-face {
  font-family: "Highland Jakarta";
  src: url("/_next/static/media/HighJakarta.d1b3bbee.woff2");
}

body {
  background: #f0f0f0;
  font-family: system-ui, -apple-system, "Segoe UI", "Roboto", "Ubuntu",
    "Cantarell", "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol", "Noto Color Emoji";
  position: relative;
  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  background-attachment: fixed !important;
  background-size: cover !important;
  @apply overflow-x-hidden duration-200 [&:has(.fav)]:overflow-hidden h-fit [interpolate-size:allow-keywords] [background:linear-gradient(to_bottom,#e5e7ebcc,#e5e7ebcc),url(/_next/static/media/blurred.0b4acc7b.avif)];
}

.squircle {
  border-radius: 30% 70% 70% 30%/30% 30% 70% 70%;
}

@supports(corner-shape: squircle) {
  .squircle {
    corner-shape: squircle;
    border-radius: 100px;
  }
}

@view-transition {
  navigation: auto;
}

[style*="view-transition-name"][style*="figure-"] {
  contain: layout;
}

::view-transition-old(root) {
  animation: none;
}

::view-transition-new(root) {
  animation: none;
}
::view-transition-old(figure) {
  animation: scaleDown 0.3s ease-out;
}

::view-transition-old(figure-caption) {
  animation: fadeOut 0.3s ease-out;
}

::view-transition-new(figure-caption) {
  animation: fadeIn 0.3s ease-in 0.1s;
}

@keyframes scaleUp {
  from {
    scale: 0.5;
    opacity: 0.5;
  }
  to {
    scale: 1;
    opacity: 1;
  }
}

@keyframes scaleDown {
  from {
    transform: scale(1);
    opacity: 1;
  }
  to {
    transform: scale(0.5);
    opacity: 0;
  }
}

@keyframes fadeOut {
  to {
    opacity: 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
nav {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 11;
    isolation: isolate;
    ul {
        list-style: none;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        transition: .2s;
        flex-wrap: wrap;
        position: relative;
        backdrop-filter: blur(10px);
        background: #f0f0f0cc;
      a {
          cursor: pointer;
          transition: .2s;
          position: relative;
          font-weight: 400;
          text-transform: lowercase;
          
        }
        &:after {
            content: '';
            position: absolute;
            position-anchor: --hovered-link;
            top: calc(anchor(bottom) - 10px);
            bottom: calc(anchor(bottom) + 0px);
            left: calc(anchor(left) + 0px);
            right: calc(anchor(right) + 0px);
            transition: .2s ease all;
            z-index: 9999999999;
            background: #d3d2d2;
            clip-path: url(#diamante-invertido);
        }
    }
    
    .active{
        anchor-name: --hovered-link;
    }
}

[data-image] figure{
  background-color: transparent !important;
  width: 100% !important;
  z-index: 3 !important;
  @media screen and (max-width:960px) {
    display: none !important;
  }
}